
const initialState = {
    user: JSON.parse(localStorage.getItem("USER-007")) || null,
    message: "THE TEST IS OK",
    texts: [
    ]
}


export default function rootReducer (state = initialState, action) {
    switch (action.type) {
        case ("REGISTER"):
            return ({ ...state,
            message: null,
            error: null,
            prevPageStatus: state.pageStatus
            });
        

        default:
            return state
    }
}