import React, { useState, useRef } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

export default function Home() {
    const [originalUrl, setOriginalUrl] = useState("")
    const [generatedUrl, setGeneratedUrl] = useState("")
    const [error, setError] = useState(false)
    const linkElementRef = useRef(null)

    const generateUrl = (originalUrl) => {
        if (!originalUrl){
            return
        } else{
            axios.post(`http://${window.location.host}/api/shorten`, {originalUrl})
                .then(res => {
                    if (res.data === "invalid Url") {
                        setError(true)
                    } else {
                        setError(false)
                    }
                    setGeneratedUrl(res.data.url)
                    setOriginalUrl("")
                })
                .catch(err => console.log(err))
        }
        return
    }

    function copyText() {
        let element = linkElementRef.current
        var range = document.createRange()
        range.selectNode(element)
        window.getSelection().removeAllRanges()
        window.getSelection().addRange(range)

        document.execCommand("copy")
        return
    }
    return (
        <>
            <div className="convert-section">
                <p>Generate shorter URLs quickly</p>
                <label htmlFor="longUrl">Paste URL to convert here</label>
                <form onSubmit={(e) => {
                    e.preventDefault();
                    generateUrl(originalUrl)
                }}>
                    <input type="text" placeholder="Enter URL"
                        onChange={(e) => {setOriginalUrl(e.target.value)}}
                    />
                </form>
                { error && <span>Please enter a valid URL</span> }

                { originalUrl &&
                    <button
                        onClick={() => generateUrl(originalUrl)}
                    >Convert</button>
                }

                { generatedUrl && 
                    <>
                        <Link ref={linkElementRef} to="" onClick={
                            () => {
                                window.location.href = generatedUrl
                            }
                        }>{generatedUrl}</Link>
                        <button className="copy-btn" onClick={() => copyText(generatedUrl)}>Copy link</button>
                    </>
                }
                
            </div>
        </>
    )
}